.collection-item {
  width: 22vw;
  display: flex;
  flex-direction: column;
  height: 450px;
  align-items: center;
  position: relative;

  .image {
    width: 100%;
    height: 95%;
    background-size: cover;
    background-position: center;
    margin-bottom: 5px;
  }
  
  &:hover {
    .custom-button {
      opacity: 0.85;
      display: flex;
    }
  }

  .C-footer {
    width: 100%;
    height: 5%;
    display: flex;
    justify-content: space-between;
    font-size: 18px;

    .name {
      width: 90%;
      margin-bottom: 15px;
    }

    .price {
      width: 10%;
    }
  }
}